html, body {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.App {

}

section {
  margin-top: 50px;
  display: flex;
  justify-content: space-around;
}

footer {
  margin-top: 24.6%;
  width: 100%;
  bottom: 0;
  position: relative;
}

.contact-div {
  display: flex;
  flex-direction: column;
}

.logo-img {
  width: 100px;
  height: 100px;
}

.App-header {
  display: flex;
  justify-content: space-around;
  color: white;
  background-color: rgba(0, 0, 0, 0.842);
}

.App-header div{
  display: flex;
  align-self: center;
}

.App-header input{
  border-radius: 20px;
  height: 30px;
  width: 500px;
  text-align: center;
}

.App-header button{
  border-radius: 20px;
  height: 21px;
  width: 100px;
}

.product-description {
  display: flex;
  flex-direction: column;
  width: 45%;
}

.product-images .btns {
  border-radius: 50px;
  width: 20px;
  height: 20px;
}

.product-images {
  width: 45%;
}

.product-images img {
  width: 90%;
  height: 70%;

}

.product-images button {
  border-radius: 20px;
  width: 40px;
  height: 40px;
  align-self: center;
}

.product-description {

  text-align: center;
  border-radius: 20px;
  padding: 50px 50px;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0px 4px 14px rgba(0, 0, 0, 0.9);
}

.product-description h1 {
  font-size: 50px;
}

.product-images div {
  display: flex;
  justify-content: space-around;
}

.product-images .quantity-container {
  display: flex;
  justify-content: center;
}

.product-images p {
  text-align: center;
}

.valor-text {
  font-weight: bold;
  font-size: 50px;

}

.contact-div img {
  width: 20px;
  height: 20px;
}
.contact-div {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.contact-div div {
  align-items: center;
}

.catalog-container {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.catalog-container img {
  border-radius: 20px;
  width: 200px;
  height: 200px;
  align-self: center;
}

.catalog-container div {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: solid 1px grey;
  border-radius: 20px;
  height: 250px;
  width: 240px;
  margin: 10px 10px;
}

@media  (max-width: 600px){
  section {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .App-header input{
    border-radius: 20px;
    height: 30px;
    width: 80%;
    text-align: center;
  }

  .product-images img {
    width: 80%;
    height: 50%;
  }

  .catalog-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
  }
}